import React, { useState, useEffect } from "react";

/**
 * StorytellingIntro - Component gii thiu theo phong cch detective story
 * To s cun ht bng cch k mt cu chuyn v n b n
 */
const StorytellingIntro = ({ onComplete }) => {
  const [currentStep, setCurrentStep] = useState(0);
  const [isTyping, setIsTyping] = useState(true);
  const [displayText, setDisplayText] = useState("");

  const storySteps = [
    {
      title: " H Ni, 2:30 sng - 05/11/2024",
      text: "Ch Nguyn Thu H., 28 tui, c pht hin t vong ti cn h chung c cao cp qun Cu Giy. Hin trng khng c du hiu t nhp, ti sn nguyn vn. Nn nhn b sit c bng dy in. Camera hnh lang b v hiu ha t 1:45 sng. Khng c nhn chng. Khng c du vt DNA.",
      image: "",
      theory: null,
    },
    {
      title: " 15 ngy iu tra - Khng manh mi",
      text: "C quan iu tra r sot mi mi quan h ca nn nhn: gia nh, bn b, ng nghip. Tt c u c alibi vng chc. Phn tch camera trong bn knh 2km - khng pht hin ngi kh nghi. Kim tra h s ti phm c tin n tng t - khng c kt qu. V n tng chng i vo ng ct...",
      image: "",
      theory: null,
    },
    {
      title: " Quyt nh t ph - p dng AI",
      text: "Lnh o Cng an TP quyt nh trin khai th nghim 'H thng Truy vt Ti phm GNN' - cng ngh mi nht v phn tch mng li. Ton b d liu: 87,000 v n hnh s t 2020-2024, 3.2 triu h s c dn, 15,000 camera an ninh c a vo phn tch  th a chiu trong 6 gi...",
      image: "",
      theory: "routine_activity",
    },
    {
      title: " Pht hin rng rn - Mng li n",
      text: "H thng GNN bo ng: pht hin 8 v n tng t trong 18 thng qua  4 qun/huyn khc nhau! Cng th on: nn nhn n c thn, 2:00-3:30 sng, sit c bng dy in, camera b v hiu ha 15-20 pht trc. Tng ng 94.7%. Cc v trc b x l ring l, khng ai nhn ra y l cng mt i tng!",
      image: "",
      theory: "crime_pattern",
    },
    {
      title: " 48 gi ph n - Bt gi thnh cng",
      text: "GNN phn tch di chuyn khng gian-thi gian, d on khu vc tip theo vi xc sut 89%. Trin khai 120 cn b, gim st 15 ta chung c trng im. Rng sng 23/11, pht hin i tng L Vn T., 34 tui, th in, ang theo di mc tiu. Bt gi ti trn, thu c dng c gy n. i tng khai nhn ton b 9 v!",
      image: "",
      theory: "hot_spots",
    },
  ];

  // Typing animation effect
  useEffect(() => {
    if (currentStep >= storySteps.length) return;

    const fullText = storySteps[currentStep].text;
    let index = 0;
    setDisplayText("");
    setIsTyping(true);

    const typingInterval = setInterval(() => {
      if (index < fullText.length) {
        setDisplayText(fullText.substring(0, index + 1));
        index++;
      } else {
        setIsTyping(false);
        clearInterval(typingInterval);
      }
    }, 30);

    return () => clearInterval(typingInterval);
  }, [currentStep]);

  const handleNext = () => {
    if (currentStep < storySteps.length - 1) {
      setCurrentStep(currentStep + 1);
    } else {
      onComplete();
    }
  };

  const handleSkip = () => {
    onComplete();
  };

  if (currentStep >= storySteps.length) {
    return null;
  }

  const step = storySteps[currentStep];

  return (
    <div className="storytelling-overlay">
      <div className="storytelling-container">
        {/* Progress dots */}
        <div className="story-progress">
          {storySteps.map((_, index) => (
            <div
              key={index}
              className={`progress-dot ${index === currentStep ? "active" : ""} ${
                index < currentStep ? "completed" : ""
              }`}
            />
          ))}
        </div>

        {/* Story content */}
        <div className="story-content">
          <div className="story-image">
            <div className="story-emoji">{step.image}</div>
          </div>

          <h2 className="story-title">{step.title}</h2>

          <div className="story-text-container">
            <p className="story-text">
              {displayText}
              {isTyping && <span className="typing-cursor"></span>}
            </p>
          </div>

          {/* Theory tag if applicable */}
          {step.theory && !isTyping && (
            <div className="theory-tag">
              <svg width="16" height="16" viewBox="0 0 24 24" fill="currentColor">
                <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm1 15h-2v-6h2v6zm0-8h-2V7h2v2z" />
              </svg>
              {step.theory === "routine_activity"
                ? "L thuyt Hot ng Thng Ngy"
                : "L thuyt Ti phm hc"}
            </div>
          )}
        </div>

        {/* Navigation buttons */}
        <div className="story-actions">
          <button className="story-btn secondary" onClick={handleSkip}>
            B qua
          </button>
          <button
            className="story-btn primary"
            onClick={handleNext}
            disabled={isTyping}
          >
            {currentStep === storySteps.length - 1 ? "Bt u khm ph" : "Tip tc"}
            <svg
              width="16"
              height="16"
              viewBox="0 0 24 24"
              fill="currentColor"
              style={{ marginLeft: "8px" }}
            >
              <path d="M8.59 16.59L13.17 12 8.59 7.41 10 6l6 6-6 6-1.41-1.41z" />
            </svg>
          </button>
        </div>

        {/* Easter egg: Click on emoji for fun animation */}
        <div className="story-hint">
           Mo: {currentStep + 1} / {storySteps.length}
        </div>
      </div>
    </div>
  );
};

export default StorytellingIntro;
