import React, { useState } from "react";
import GoogleMapHotspot from "../shared/GoogleMapHotspot";
import { hotspotLocations, hanoiCenter, defaultZoom } from "../../data/hotspotData";

/**
 * Hotspot Prediction Tab V2 - vi Google Maps tht
 * Tham kho: Palantir Gotham, PredPol, ArcGIS Crime Analysis
 */
const HotspotPredictionTabV2 = () => {
  const [selectedTimeframe, setSelectedTimeframe] = useState("next_7_days");
  const [selectedCrimeType, setSelectedCrimeType] = useState("all");
  const [selectedWard, setSelectedWard] = useState("all");
  const [showHeatmap, setShowHeatmap] = useState(true);
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [analysisComplete, setAnalysisComplete] = useState(false);
  const [filteredHotspots, setFilteredHotspots] = useState(hotspotLocations);
  const [activeAction, setActiveAction] = useState(null); // "deploy", "detail", "alert"
  const [selectedHotspot, setSelectedHotspot] = useState(null);

  // X l cc action buttons - Hin th trong giao din
  const handleDeployPatrol = (hotspot) => {
    setSelectedHotspot(hotspot);
    setActiveAction("deploy");
  };

  const handleViewDetail = (hotspot) => {
    setSelectedHotspot(hotspot);
    setActiveAction("detail");
  };

  const handleSetAlert = (hotspot) => {
    setSelectedHotspot(hotspot);
    setActiveAction("alert");
  };

  const closeActionPanel = () => {
    setActiveAction(null);
    setSelectedHotspot(null);
  };

  // X l phn tch
  const handleAnalyze = () => {
    setIsAnalyzing(true);
    setAnalysisComplete(false);

    // Simulate analysis process
    setTimeout(() => {
      // Filter hotspots based on criteria
      let filtered = [...hotspotLocations];

      // Filter by crime type
      if (selectedCrimeType !== "all") {
        filtered = filtered.filter((hotspot) =>
          hotspot.crimeTypes.some((type) => type.toLowerCase().includes(selectedCrimeType.toLowerCase()))
        );
      }

      // Filter by ward (phng)
      if (selectedWard !== "all") {
        filtered = filtered.filter((hotspot) => hotspot.ward === selectedWard);
      }

      // Sort by probability (highest first)
      filtered.sort((a, b) => b.probability - a.probability);

      setFilteredHotspots(filtered);
      setIsAnalyzing(false);
      setAnalysisComplete(true);
    }, 2000);
  };

  const getRiskColor = (risk) => {
    switch (risk) {
      case "cao":
        return "#e74c3c";
      case "trung bnh":
        return "#f39c12";
      case "thp":
        return "#27ae60";
      default:
        return "#6b7c93";
    }
  };

  const getTrendIcon = (trend) => {
    switch (trend) {
      case "tng":
      case "tng mnh":
        return "";
      case "gim":
        return "";
      case "n nh":
        return "";
      default:
        return "";
    }
  };

  // Get unique districts for filter
  const uniqueWards = [...new Set(hotspotLocations.map((h) => h.ward))];

  // Statistics
  const stats = {
    total: filteredHotspots.length,
    high: filteredHotspots.filter((h) => h.risk === "cao").length,
    medium: filteredHotspots.filter((h) => h.risk === "trung bnh").length,
    low: filteredHotspots.filter((h) => h.risk === "thp").length,
    avgProbability: Math.round(
      filteredHotspots.reduce((sum, h) => sum + h.probability, 0) / filteredHotspots.length
    ),
    totalIncidents: filteredHotspots.reduce((sum, h) => sum + h.incidentCount30days, 0),
  };

  return (
    <div className="hotspot-prediction-tab-v2">
      {/* Header vi controls */}
      <div className="hotspot-header">
        <div className="hotspot-title-section">
          <h2 className="hotspot-title"> D Bo im Nng Ti Phm</h2>
          <p className="hotspot-subtitle">
            Phn tch khng gian-thi gian d on khu vc c nguy c cao  Powered by GNN + Google Maps
          </p>
        </div>
      </div>

      {/* Filter Panel */}
      <div className="filter-panel">
        <div className="filter-group">
          <label className="filter-label">
            <span className="label-icon"></span>
            Khung thi gian d bo:
          </label>
          <select
            className="filter-select"
            value={selectedTimeframe}
            onChange={(e) => setSelectedTimeframe(e.target.value)}
          >
            <option value="next_24h">24 gi ti</option>
            <option value="next_3_days">3 ngy ti</option>
            <option value="next_7_days">7 ngy ti (Khuyn ngh)</option>
            <option value="next_14_days">14 ngy ti</option>
            <option value="next_30_days">30 ngy ti</option>
          </select>
        </div>

        <div className="filter-group">
          <label className="filter-label">
            <span className="label-icon"></span>
            Loi ti phm:
          </label>
          <select
            className="filter-select"
            value={selectedCrimeType}
            onChange={(e) => setSelectedCrimeType(e.target.value)}
          >
            <option value="all">Tt c loi</option>
            <option value="trm cp">Trm cp</option>
            <option value="cp">Cp git</option>
            <option value="t nhp">t nhp</option>
            <option value="mc ti">Mc ti</option>
            <option value="xe">Lin quan xe</option>
            <option value="la o">La o</option>
          </select>
        </div>

        <div className="filter-group">
          <label className="filter-label">
            <span className="label-icon"></span>
            Khu vc:
          </label>
          <select
            className="filter-select"
            value={selectedWard}
            onChange={(e) => setSelectedWard(e.target.value)}
          >
            <option value="all">Ton thnh ph</option>
            {uniqueWards.map((ward) => (
              <option key={ward} value={ward}>
                {ward}
              </option>
            ))}
          </select>
        </div>

        <button
          className={`analyze-button ${isAnalyzing ? "analyzing" : ""}`}
          onClick={handleAnalyze}
          disabled={isAnalyzing}
        >
          {isAnalyzing ? (
            <>
              <span className="spinner"></span>
              ang phn tch...
            </>
          ) : (
            <>
              <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                <path d="M9 11H7v2h2v-2zm4 0h-2v2h2v-2zm4 0h-2v2h2v-2zm2-7h-1V2h-2v2H8V2H6v2H5c-1.11 0-1.99.9-1.99 2L3 20c0 1.1.89 2 2 2h14c1.1 0 2-.9 2-2V6c0-1.1-.9-2-2-2zm0 16H5V9h14v11z" />
              </svg>
               Bt u Phn tch D bo
            </>
          )}
        </button>

        <button
          className={`toggle-heatmap ${showHeatmap ? "active" : ""}`}
          onClick={() => setShowHeatmap(!showHeatmap)}
        >
          {showHeatmap ? " Tt" : " Bt"} Lp Nhit
        </button>
      </div>

      {/* Analysis Progress Indicator */}
      {isAnalyzing && (
        <div className="analysis-progress">
          <div className="progress-bar">
            <div className="progress-fill"></div>
          </div>
          <p className="progress-text">
            ang phn tch d liu 87,000 v n  3.2M h s  15K camera...
          </p>
        </div>
      )}

      {/* Success Message */}
      {analysisComplete && !isAnalyzing && (
        <div className="analysis-complete">
          <div className="complete-icon"></div>
          <div className="complete-text">
            <strong>Phn tch hon tt!</strong>
            <p>
              Tm thy {filteredHotspots.length} im nng ph hp vi tiu ch lc. H thng khuyn
              ngh u tin {stats.high} khu vc c nguy c cao.
            </p>
          </div>
        </div>
      )}

      {/* Main Content */}
      <div className="hotspot-content">
        {/* Google Maps */}
        <div className="map-section">
          <div className="map-container-google">
            <GoogleMapHotspot
              hotspots={filteredHotspots}
              center={hanoiCenter}
              zoom={defaultZoom}
              showHeatmap={showHeatmap}
            />
          </div>

          {/* Map Legend */}
          <div className="map-legend-panel">
            <div className="legend-title"> Ch gii:</div>
            <div className="legend-items-grid">
              <div className="legend-item">
                <div className="legend-marker high"></div>
                <span>Nguy c Cao (&gt;80%)</span>
                <strong>{stats.high}</strong>
              </div>
              <div className="legend-item">
                <div className="legend-marker medium"></div>
                <span>Nguy c TB (50-80%)</span>
                <strong>{stats.medium}</strong>
              </div>
              <div className="legend-item">
                <div className="legend-marker low"></div>
                <span>Nguy c Thp (&lt;50%)</span>
                <strong>{stats.low}</strong>
              </div>
            </div>
          </div>
        </div>

        {/* Hotspots List */}
        <div className="hotspots-list-section">
          <div className="list-header">
            <h3>
               Danh Sch im Nng u Tin
              {selectedWard !== "all" && ` - ${selectedWard}`}
            </h3>
            <span className="list-count">
              {filteredHotspots.length} khu vc
              {stats.high > 0 && `  ${stats.high} CAO`}
            </span>
          </div>

          {filteredHotspots.length === 0 ? (
            <div className="no-results">
              <div className="no-results-icon"></div>
              <p>Khng tm thy im nng ph hp vi tiu ch lc.</p>
              <button className="reset-filters-btn" onClick={() => {
                setSelectedCrimeType("all");
                setSelectedWard("all");
                handleAnalyze();
              }}>
                 t li b lc
              </button>
            </div>
          ) : (
            <div className="hotspots-grid">
              {filteredHotspots.map((hotspot, idx) => (
                <div key={hotspot.id} className="hotspot-card" style={{ animationDelay: `${idx * 0.1}s` }}>
                  <div className="hotspot-card-header">
                    <div className="hotspot-location">
                      <div className="location-area">{hotspot.area}</div>
                    </div>
                    <div
                      className="risk-badge"
                      style={{
                        background: `rgba(${
                          hotspot.risk === "cao"
                            ? "231, 76, 60"
                            : hotspot.risk === "trung bnh"
                            ? "243, 156, 18"
                            : "39, 174, 96"
                        }, 0.2)`,
                        color: getRiskColor(hotspot.risk),
                        border: `1px solid ${getRiskColor(hotspot.risk)}`,
                      }}
                    >
                      {hotspot.risk.toUpperCase()}
                    </div>
                  </div>

                  <div className="probability-section">
                    <div className="probability-label">Xc sut xy ra trong {selectedTimeframe === "next_7_days" ? "7 ngy" : selectedTimeframe === "next_24h" ? "24h" : "30 ngy"} ti:</div>
                    <div className="probability-bar-container">
                      <div
                        className="probability-bar"
                        style={{
                          width: `${hotspot.probability}%`,
                          background: getRiskColor(hotspot.risk),
                        }}
                      >
                        <span className="probability-value">{hotspot.probability}%</span>
                      </div>
                    </div>
                  </div>

                  <div className="hotspot-details">
                    <div className="detail-item">
                      <span className="detail-icon"></span>
                      <span className="detail-text">{hotspot.crimeTypes.join(", ")}</span>
                    </div>
                    <div className="detail-item">
                      <span className="detail-icon"></span>
                      <span className="detail-text">{hotspot.timePattern}</span>
                    </div>
                    <div className="detail-item">
                      <span className="detail-icon"></span>
                      <span className="detail-text">Cao im: {hotspot.peak}</span>
                    </div>
                    <div className="detail-item">
                      <span className="detail-icon">{getTrendIcon(hotspot.trend)}</span>
                      <span className="detail-text">
                        Xu hng: <strong>{hotspot.trend}</strong> ({hotspot.trendPercent > 0 ? "+" : ""}{hotspot.trendPercent}%)
                      </span>
                    </div>
                    <div className="detail-item">
                      <span className="detail-icon"></span>
                      <span className="detail-text">V xy ra 30 ngy qua: <strong>{hotspot.incidentCount30days}</strong></span>
                    </div>
                  </div>

                  <div className="risk-factors">
                    <div className="factors-title"> Yu t ri ro:</div>
                    <div className="factors-tags">
                      {hotspot.factors.map((factor, idx) => (
                        <span key={idx} className="factor-tag">
                          {factor}
                        </span>
                      ))}
                    </div>
                  </div>

                  <div className="hotspot-actions">
                    <button
                      className="action-btn-small deploy"
                      onClick={() => handleDeployPatrol(hotspot)}
                      title="Trin khai lc lng tun tra"
                    >
                       Trin khai Tun tra
                    </button>
                    <button
                      className="action-btn-small detail"
                      onClick={() => handleViewDetail(hotspot)}
                      title="Xem thng tin chi tit"
                    >
                       Xem Chi tit
                    </button>
                    <button
                      className="action-btn-small alert"
                      onClick={() => handleSetAlert(hotspot)}
                      title="Thit lp cnh bo t ng"
                    >
                       Thit lp Cnh bo
                    </button>
                  </div>

                  <div className="last-incident">
                    V gn nht: <strong>{hotspot.lastIncident}</strong>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>

      {/* Statistics Dashboard */}
      <div className="statistics-dashboard">
        <div className="dashboard-title"> Tng Quan Phn Tch</div>
        <div className="stats-grid">
          <div className="stat-card">
            <div className="stat-icon red"></div>
            <div className="stat-content">
              <div className="stat-value">{stats.high}</div>
              <div className="stat-label">im nng Cao</div>
            </div>
          </div>
          <div className="stat-card">
            <div className="stat-icon orange"></div>
            <div className="stat-content">
              <div className="stat-value">{stats.medium}</div>
              <div className="stat-label">im nng Trung bnh</div>
            </div>
          </div>
          <div className="stat-card">
            <div className="stat-icon blue"></div>
            <div className="stat-content">
              <div className="stat-value">{stats.avgProbability}%</div>
              <div className="stat-label">Xc sut Trung bnh</div>
            </div>
          </div>
          <div className="stat-card">
            <div className="stat-icon purple"></div>
            <div className="stat-content">
              <div className="stat-value">{stats.totalIncidents}</div>
              <div className="stat-label">V trong 30 ngy</div>
            </div>
          </div>
        </div>
      </div>

      {/* Recommendation Panel */}
      {stats.high > 0 && (
        <div className="recommendation-panel">
          <div className="recommendation-header">
            <div className="rec-icon"></div>
            <h4>Khuyn Ngh Hnh ng</h4>
          </div>
          <div className="recommendation-content">
            <div className="rec-item priority">
              <strong> u tin cao nht:</strong>
              <p>
                Trin khai lc lng tun tra tng cng ti {stats.high} khu vc nguy c cao, c bit l{" "}
                {filteredHotspots
                  .filter((h) => h.risk === "cao")
                  .slice(0, 3)
                  .map((h) => h.district)
                  .join(", ")}
              </p>
            </div>
            <div className="rec-item">
              <strong> Khung gi:</strong>
              <p>Tp trung vo khung gi 18:00 - 23:00 v 01:00 - 05:00 (chim 70% cc v)</p>
            </div>
            <div className="rec-item">
              <strong> Bin php:</strong>
              <p>Nng cp h thng camera, tng cng nh sng ng ph, phi hp vi ban qun l khu vc</p>
            </div>
          </div>
        </div>
      )}

      {/* Action Panel - Hin th trong giao din thay v alert */}
      {activeAction && selectedHotspot && (
        <div className="action-panel-overlay" onClick={closeActionPanel}>
          <div className="action-panel" onClick={(e) => e.stopPropagation()}>
            <div className="action-panel-header">
              <h3>
                {activeAction === "deploy" && " Trin khai Tun tra"}
                {activeAction === "detail" && " Chi tit im nng"}
                {activeAction === "alert" && " Thit lp Cnh bo"}
              </h3>
              <button className="close-btn" onClick={closeActionPanel}></button>
            </div>

            <div className="action-panel-content">
              {/* Deploy Patrol */}
              {activeAction === "deploy" && (
                <div className="deploy-panel">
                  <div className="info-row">
                    <strong>Phng:</strong> {selectedHotspot.ward}
                  </div>
                  <div className="info-row">
                    <strong>Khu vc:</strong> {selectedHotspot.area}
                  </div>
                  <div className="info-row">
                    <strong>Mc  ri ro:</strong>
                    <span className={`risk-level ${selectedHotspot.risk}`}>
                      {selectedHotspot.risk.toUpperCase()}
                    </span>
                  </div>
                  <div className="info-row">
                    <strong>Xc sut:</strong> {selectedHotspot.probability}%
                  </div>
                  <div className="info-row">
                    <strong>Thi gian tng cng:</strong> {selectedHotspot.timePattern}
                  </div>
                  <div className="info-row">
                    <strong>Ta :</strong> {selectedHotspot.lat}, {selectedHotspot.lng}
                  </div>
                  <div className="recommendation-box">
                    <strong> Khuyn ngh:</strong>
                    <p>{selectedHotspot.recommendation}</p>
                  </div>
                  <button className="action-confirm-btn deploy">
                     Xc nhn trin khai
                  </button>
                </div>
              )}

              {/* View Detail */}
              {activeAction === "detail" && (
                <div className="detail-panel">
                  <div className="info-row">
                    <strong>Phng:</strong> {selectedHotspot.ward}
                  </div>
                  <div className="info-row">
                    <strong>Khu vc:</strong> {selectedHotspot.area}
                  </div>
                  <div className="info-row">
                    <strong>Mc :</strong>
                    <span className={`risk-level ${selectedHotspot.risk}`}>
                      {selectedHotspot.risk.toUpperCase()}
                    </span>
                  </div>
                  <div className="info-row">
                    <strong>Xc sut:</strong> {selectedHotspot.probability}%
                  </div>
                  <div className="info-section">
                    <strong> Loi ti phm:</strong>
                    <ul>
                      {selectedHotspot.crimeTypes.map((type, idx) => (
                        <li key={idx}>{type}</li>
                      ))}
                    </ul>
                  </div>
                  <div className="info-section">
                    <strong> Thi gian cao im:</strong> {selectedHotspot.timePattern}
                  </div>
                  <div className="info-section">
                    <strong> Ngy cao im:</strong> {selectedHotspot.peak}
                  </div>
                  <div className="info-section">
                    <strong> Yu t nguy c:</strong>
                    <ul>
                      {selectedHotspot.factors.map((factor, idx) => (
                        <li key={idx}>{factor}</li>
                      ))}
                    </ul>
                  </div>
                  <div className="info-row">
                    <strong> Xu hng:</strong> {selectedHotspot.trend} ({selectedHotspot.trendPercent > 0 ? '+' : ''}{selectedHotspot.trendPercent}%)
                  </div>
                  <div className="info-row">
                    <strong> V gn nht:</strong> {selectedHotspot.lastIncident}
                  </div>
                  <div className="info-row">
                    <strong> S v 30 ngy:</strong> {selectedHotspot.incidentCount30days}
                  </div>
                  <div className="recommendation-box">
                    <strong> Khuyn ngh:</strong>
                    <p>{selectedHotspot.recommendation}</p>
                  </div>
                </div>
              )}

              {/* Set Alert */}
              {activeAction === "alert" && (
                <div className="alert-panel">
                  <div className="info-row">
                    <strong>Phng:</strong> {selectedHotspot.ward}
                  </div>
                  <div className="info-row">
                    <strong>Khu vc:</strong> {selectedHotspot.area}
                  </div>
                  <div className="info-row">
                    <strong>Mc :</strong>
                    <span className={`risk-level ${selectedHotspot.risk}`}>
                      {selectedHotspot.risk.toUpperCase()}
                    </span>
                  </div>
                  <div className="info-row">
                    <strong>Thi gian:</strong> {selectedHotspot.timePattern}
                  </div>
                  <div className="alert-settings">
                    <h4> Nhn thng bo qua:</h4>
                    <label className="checkbox-label">
                      <input type="checkbox" defaultChecked /> SMS
                    </label>
                    <label className="checkbox-label">
                      <input type="checkbox" defaultChecked /> Email
                    </label>
                    <label className="checkbox-label">
                      <input type="checkbox" defaultChecked /> Push notification
                    </label>
                  </div>
                  <div className="alert-triggers">
                    <h4>Khi c:</h4>
                    <label className="checkbox-label">
                      <input type="checkbox" defaultChecked /> V vic mi pht sinh
                    </label>
                    <label className="checkbox-label">
                      <input type="checkbox" defaultChecked /> Thay i mc  ri ro
                    </label>
                    <label className="checkbox-label">
                      <input type="checkbox" defaultChecked /> Cp nht xu hng
                    </label>
                  </div>
                  <button className="action-confirm-btn alert">
                     Xc nhn thit lp cnh bo
                  </button>
                </div>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default HotspotPredictionTabV2;
