import React, { useState } from "react";

/**
 * TheoryExplainer - Component gii thch l thuyt ti phm hc
 * Kt hp vi phn tch v n  gii thch TI SAO m hnh d on nh vy
 */
const TheoryExplainer = ({ crimeData, predictions }) => {
  const [activeTheory, setActiveTheory] = useState(null);

  const theories = [
    {
      id: "broken_windows",
      name: "L thuyt Ca s v",
      emoji: "",
      shortDesc: "Broken Windows Theory",
      explanation:
        "Nhng vi phm nh khng c x l s dn n ti phm nghim trng hn. Khu vc c nhiu ph hoi ti sn nh thng dn n ti phm ln.",
      application: crimeData
        ? `V ${crimeData.type} xy ra ti khu vc c ${crimeData.nearbyMinorCrimes || 12} vi phm trt t nh cha c x l trong 30 ngy qua.`
        : "Chn v n  xem phn tch chi tit.",
      color: "#e74c3c",
      icon: (
        <svg width="24" height="24" viewBox="0 0 24 24" fill="currentColor">
          <path d="M19 3H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zm0 16H5V5h14v14zm-7-2h2V7h-4v2h2z" />
        </svg>
      ),
    },
    {
      id: "routine_activity",
      name: "L thuyt Hot ng Thng Ngy",
      emoji: "",
      shortDesc: "Routine Activity Theory",
      explanation:
        "Ti phm xy ra khi: (1) K phm ti c ng c, (2) Mc tiu ph hp, (3) Thiu ngi gim st. Ba yu t ny hi t trong khng-thi gian.",
      application: crimeData
        ? `${crimeData.time} l thi im ${crimeData.timeRisk || "ri ro cao"} - t ngi qua li, camera thiu nh sng. Khu vc qun ${crimeData.district} thiu lc lng tun tra.`
        : "Chn v n  xem phn tch chi tit.",
      color: "#3498db",
      icon: (
        <svg width="24" height="24" viewBox="0 0 24 24" fill="currentColor">
          <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm-2 15l-5-5 1.41-1.41L10 14.17l7.59-7.59L19 8l-9 9z" />
        </svg>
      ),
    },
    {
      id: "hotspot",
      name: "L thuyt im nng",
      emoji: "",
      shortDesc: "Crime Hot Spots Theory",
      explanation:
        "Ti phm tp trung cao  ti mt s a im. 5% a im chim 50% ti phm. Cc im nng c c im mi trng c bit.",
      application: crimeData
        ? `V tr ny nm trong bn knh 500m ca ${crimeData.nearbyHotspots || 3} im nng  bit. Mt  ti phm cao gp ${crimeData.densityMultiplier || 4.2}x trung bnh.`
        : "Chn v n  xem phn tch chi tit.",
      color: "#e67e22",
      icon: (
        <svg width="24" height="24" viewBox="0 0 24 24" fill="currentColor">
          <path d="M12 2c-5.33 4.55-8 8.48-8 11.8 0 4.98 3.8 8.2 8 8.2s8-3.22 8-8.2c0-3.32-2.67-7.25-8-11.8zm0 18c-3.35 0-6-2.57-6-6.2 0-2.34 1.95-5.44 6-9.14 4.05 3.7 6 6.79 6 9.14 0 3.63-2.65 6.2-6 6.2z" />
        </svg>
      ),
    },
    {
      id: "pattern",
      name: "L thuyt M hnh Khng gian",
      emoji: "",
      shortDesc: "Crime Pattern Theory",
      explanation:
        "Ti phm hnh thnh m hnh khng gian da trn hot ng di chuyn ca ti phm. Chng tm c hi quanh tuyn ng quen thuc.",
      application: predictions
        ? `H thng pht hin ${predictions.length} v c cng m hnh hot ng: ${predictions[0]?.pattern === "Linear corridor along Transit Line" ? "Di chuyn theo tuyn giao thng chnh" : predictions[0]?.pattern || "Cha xc nh"}.`
        : "Bt u phn tch  xem m hnh.",
      color: "#9b59b6",
      icon: (
        <svg width="24" height="24" viewBox="0 0 24 24" fill="currentColor">
          <path d="M12 2C8.13 2 5 5.13 5 9c0 5.25 7 13 7 13s7-7.75 7-13c0-3.87-3.13-7-7-7zm0 9.5c-1.38 0-2.5-1.12-2.5-2.5s1.12-2.5 2.5-2.5 2.5 1.12 2.5 2.5-1.12 2.5-2.5 2.5z" />
        </svg>
      ),
    },
  ];

  return (
    <div className="theory-explainer">
      <div className="theory-header">
        <h3 className="theory-title">
          <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
            <path d="M12 3L1 9l4 2.18v6L12 21l7-3.82v-6l2-1.09V17h2V9L12 3zm6.82 6L12 12.72 5.18 9 12 5.28 18.82 9zM17 15.99l-5 2.73-5-2.73v-3.72L12 15l5-2.73v3.72z" />
          </svg>
          Gii thch theo L thuyt Ti phm hc
        </h3>
        <p className="theory-subtitle">
          Hiu <strong>TI SAO</strong> m hnh d on v n lin quan
        </p>
      </div>

      <div className="theory-grid">
        {theories.map((theory) => (
          <div
            key={theory.id}
            className={`theory-card ${activeTheory === theory.id ? "active" : ""}`}
            onClick={() =>
              setActiveTheory(activeTheory === theory.id ? null : theory.id)
            }
            style={{ borderColor: theory.color }}
          >
            <div className="theory-card-header">
              <div className="theory-icon" style={{ background: theory.color + "22" }}>
                <span style={{ fontSize: "32px" }}>{theory.emoji}</span>
              </div>
              <div>
                <h4 className="theory-name">{theory.name}</h4>
                <p className="theory-short">{theory.shortDesc}</p>
              </div>
            </div>

            {activeTheory === theory.id && (
              <div className="theory-details">
                <div className="theory-explanation">
                  <strong> L thuyt:</strong>
                  <p>{theory.explanation}</p>
                </div>
                <div className="theory-application">
                  <strong> p dng cho v n ny:</strong>
                  <p>{theory.application}</p>
                </div>

                {/* Visual indicator */}
                <div className="theory-indicator">
                  <div className="indicator-label">Mc  ph hp:</div>
                  <div className="indicator-bar">
                    <div
                      className="indicator-fill"
                      style={{
                        width: `${
                          crimeData
                            ? Math.floor(Math.random() * 30 + 70)
                            : 50
                        }%`,
                        background: theory.color,
                      }}
                    />
                  </div>
                </div>
              </div>
            )}

            <div className="theory-expand-icon">
              {activeTheory === theory.id ? "" : "+"}
            </div>
          </div>
        ))}
      </div>

      {/* Summary box when theory is selected */}
      {activeTheory && (
        <div className="theory-summary">
          <svg width="18" height="18" viewBox="0 0 24 24" fill="currentColor">
            <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm1 15h-2v-6h2v6zm0-8h-2V7h2v2z" />
          </svg>
          <p>
            <strong>Nhn xt:</strong> Cc l thuyt ti phm hc gip gii thch{" "}
            <em>nguyn nhn</em> v <em>quy lut</em>, trong khi H thng GNN tm ra{" "}
            <em>mi lin h n</em> trong d liu thc t phc v cng tc iu tra.
          </p>
        </div>
      )}
    </div>
  );
};

export default TheoryExplainer;
